.stars {
  display: flex;
  height: 16px;

  &__star {
    height: 16px;
    width: 16px;
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 4px;

    .stars--1 &:first-child {
      background-image: url(../images/star-active.svg);
    }

    .stars--2 &:nth-last-child(1n+4) {
      background-image: url(../images/star-active.svg);
    }

    .stars--3 &:nth-last-child(1n+3) {
      background-image: url(../images/star-active.svg);
    }

    .stars--4 &:nth-last-child(1n+2) {
      background-image: url(../images/star-active.svg);
    }

    .stars--5 &:nth-last-child(1n+1) {
      background-image: url(../images/star-active.svg);
    }
  }
}
